<template>
  <div>
    <router-view></router-view>
    <div class="tabr">
      <div
        :class="{active: item.isActive}"
        v-for="(item, index) in tabbar"
        :key="index"
        @click="tabBar(item)"
      >
        {{item.title}}
        <img :src="item.url" alt />
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/home.less";
import TakeOutFoodVue from "./Home/TakeOutFood.vue";
export default {
  data() {
    return {
      tabbar: [
        {
          icon: "coupon-o",
          title: "咖啡",
          Name: "TakeOutFood",
          isActive: false,
          url: require("../assets/images/svg.png"),
          routeName: ["home/takeoutfood"]
        },
        {
          icon: "orders-o",
          title: "订单",
          Name: "Find",
          isActive: false,
          url: require("../assets/images/xian.png"),
          routeName: ["home/find"]
        },
        {
          icon: "shopping-cart-o",
          title: "购物车",
          Name: "Order",
          isActive: false,
          url: require("../assets/images/gou.png"),
          routeName: ["home/order"]
        },
        {
          icon: "contact",
          title: "我的",
          Name: "My",
          isActive: false,
          url: require("../assets/images/my.png"),
          routeName: ["home/my"]
        }
      ]
    };
  },
  created() {
    let url = location.hash
      .slice(2)
      .split("/")
      .splice(0, 2)
      .join("/");
    for (let i = 0; i < this.tabbar.length; i++) {
      if (this.tabbar[i].routeName.indexOf(url) > -1) {
        this.tabbar[i].isActive = true;
        break;
      }
    }
  },
  methods: {
    tabBar(item) {
      if (item.isActive) {
        return;
      }

      for (let i = 0; i < this.tabbar.length; i++) {
        if (this.tabbar[i].isActive) {
          this.tabbar[i].isActive = false;
          break;
        }
      }

      item.isActive = true;

      this.$router.push({ name: item.Name });
    }
  }
};
</script>
